button {
  all: unset;
  padding: 6px 16px;
  background: var(--red);
  height: 32px;
  font-weight: 5000;
  color: white;
}

.hero {
  display: flex;
  align-items: center;
  /* height: calc(100vh-72px); */
}

.hero-content {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.hero-content h1 {
  font-weight: 800;
  line-height: 102px;
  font-size: 108px;
}

.hero-content p {
  font-weight: 600;
  color: var(--grey);
  max-width: 400px;
}

.hero-btn {
  display: flex;
  gap: 40px;
}

.secondary-btn {
  background: transparent;
  color: var(--grey);
  border: 1px solid gray;
}

.shopping p {
  font-style: 14px;
  margin-bottom: 8px;
}

.brand-icons {
  display: flex;
  gap: 40px;
}
